<template>
   <nav class="navbar navbar-expand-lg navbar-light justify-content-center
   border border-start-0 border-end-0 border-top border-bottom">
      <ul class="navbar-nav flex-row overflow-auto navbar-custom-scroll">
        <li class="nav-item nav-link text-nowrap px-2" :class="{active: category === 'all' }"
        @click="category = 'all'">全部</li>
        <li class="nav-item nav-link text-nowrap px-2" :class="{active: category === '登山' }"
        @click="category = '登山'">
          登山 </li>
        <li class="nav-item nav-link text-nowrap px-2" :class="{active: category == '攀岩' }"
        @click="category = '攀岩'">攀岩</li>
        <li class="nav-item nav-link text-nowrap px-2" :class="{active: category == '溯溪' }"
        @click="category = '溯溪'">溯溪</li>
      </ul>
    </nav>
    <div class="container mt-md-5 mt-3 mb-7">
      <div class="row row-cols-1 row-cols-sm-2 row-cols-md-4">
        <div class="col" v-for="item in filterDate" :key="item.id">
          <div class="card border-0 mb-4 position-relative">
            <div :style="{backgroundImage: `url(${item.imageUrl})`}"
            style="height: 300px; background-size: cover; background-position: center center"></div>
            <a href="#" class="text-dark">
              <!-- <i class="far fa-heart position-absolute" style="right: 16px; top: 16px"></i> -->
            </a>
            <div class="card-body p-0">
              <h4 class="mb-0 mt-3"><router-link :to="`/product/${item.id}`">
              {{item.title}}</router-link></h4>
              <p class="card-text text-muted mb-0"
              style="height: 4rem; overflow: hidden">{{item.content}}</p>
              <p class="text-muted mt-3">NT$ {{item.price}}元</p>
            </div>
          </div>
        </div>
      </div>
      <div v-if="category === 'all'" >
      <Page :pagination-data="pagination" @get="getProducts"/>
      </div>
      <!-- <nav class="d-flex justify-content-center">
        <ul class="pagination">
          <li class="page-item">
            <a class="page-link" href="#" aria-label="Previous">
              <span aria-hidden="true">&laquo;</span>
            </a>
          </li>
          <li class="page-item active"><a class="page-link" href="#">1</a></li>
          <li class="page-item"><a class="page-link" href="#">2</a></li>
          <li class="page-item"><a class="page-link" href="#">3</a></li>
          <li class="page-item">
            <a class="page-link" href="#" aria-label="Next">
              <span aria-hidden="true">&raquo;</span>
            </a>
          </li>
        </ul>
      </nav> -->
    </div>
</template>

<script>
import Page from '@/components/Page.vue';

export default {
  components: { Page },
  data() {
    return {
      products: [{ category: '' }],
      category: 'all',
      pagination: '1',
      // link: 'a',
    };
  },
  methods: {
    getProducts(page = 1) {
      console.log(page);
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/products/?page=${page}`;
      this.$http.get(url)
        .then((res) => {
          console.log('74', res);
          this.products = res.data.products;
          console.log(this.products);
          this.pagination = res.data.pagination;
        });
    },
  },
  computed: {
    filterDate() {
      if (this.category === 'all') {
        return this.products;
      }
      return this.products.filter((item) => item.category === this.category);
    },
  },
  mounted() {
    this.getProducts();
    console.log(this.$route.params.category);
    if (this.$route.params?.category?.length > 0) {
      this.category = this.$route.params.category;
    }
  },
};
</script>
